@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
@import url(https://fonts.googleapis.com/css?family=Rubik);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  padding: 0;
}

.app {
  background-color: #eee;
  text-align: center;
  display: grid;
  grid-template-areas:
    "n n n n n n n n n n n n"
    "c c c c c c c c c c c c"
    "f f f f f f f f f f f f";
}

.mynavbar {
  grid-area: n;
}

.content {
  grid-area: c;
  min-height: 80vh;
}

.footer {
  grid-area: f;
  margin-top: 20px;
}

button {
  margin-left: 15px;
}
span {
  color: white;
  margin-left: 10px;
}

.mynavbar li {
  padding: 0 6px 0 6px;
}

.register {
  width: 30%;
  display: block;
  margin: auto;
  padding: 10px;
}

.register input {
  margin: 35px 0 12px;
}

.register span {
  color: black;
  font-size: 16px;
  margin-top: -7px;
  word-wrap: break-word;
}

.register button {
  margin: 20px auto;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1px;
}

.contact {
  padding-top: 20px;
}

.contact span {
  color: steelblue;
  font-size: 24px;
}

.contact textarea {
  width: 50%;
  min-height: 150px;
  margin: 10px auto;
}

.contact select {
  width: 20%;
  margin: 20px auto;
  display: inline-block;
}

.contact label {
  margin-right: 30px;
}

.contact button {
  text-transform: uppercase;
  font-weight: bold;
}

.message {
  margin: 20px;
}

.products-item {
  border: solid 1px #e2e2e2;
  border-radius: 10px;
  margin-left: 50px;
  margin-right: auto;
  margin-top: 20px;
  width: 33%;
  display: inline-block;
  background-color: white;
}

.products-item:hover {
  cursor: pointer;
}

.products-container {
  /* border: solid 2px orange; */
}

.products-item button {
  width: 100%;
  height: 60px;
  margin-left: 0px;
  margin-right: auto;
  margin-top: 15px;
  font-size: 20px;
  border: none;
  border-top: solid #e5e5e5 0.5px;
  background-color: #efefef;
  border-radius: 0px 0px 10px 10px;
  outline: none;
  font-weight: 700;
}

.text-link:hover {
  color: white;
  text-decoration: none;
}

.products-item button:hover {
  background-color: orange;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  color: white;
}

.products-item img {
  margin-top: 20px;
}
.productname {
  font-family: "Rubik", sans-serif;
  font-size: 25px;
  margin-top: 15px;
  text-align: center;
}

.description {
  text-align: center;
